html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;box-sizing:border-box}body{font-family:'Nunito',  sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:url(data:image/png;base64,);background-repeat:repeat center center fixed;font-size:14px;line-height:1.25}.App{margin:0 auto;max-width:990px;background-color:white}.App__content{padding:30px}h1{font-family:'Raleway', sans-serif;font-weight:600;color:#FFA5B6;font-size:44px;line-height:1.5;margin-bottom:20px}h2{font-weight:600;color:#779A7E;font-size:32px;line-height:1.5;margin-bottom:15px}h3{font-weight:700;color:#13928C;line-height:1.33;font-size:18px}h4{font-size:16px}.text-note{color:#779A7E;font-style:italic}.minus-button,.plus-button{height:40px;width:40px;cursor:pointer;display:flex;justify-content:center;align-items:center;border:none;border-radius:50%;color:white}.minus-button.disabled,.plus-button.disabled{background-color:#d3d3d3;cursor:default}.minus-button{background-color:#B2DBD9}.plus-button{background-color:#FFA5B6}

.Header__image{display:block;max-width:100%}.Header__navbar{height:30px;background-color:#fae2a5}

.IslandersContainer{margin-bottom:30px}@media (min-width: 960px){.IslandersContainer .ResidentsInputContainer{margin-right:30px}}

.ResidentsInputContainer{padding:15px 20px;max-width:450px}.ResidentsInputContainer h3{margin-bottom:10px}.ResidentsInputContainer__fields{margin-top:15px}.ResidentsInputContainer__fields .ResidentNameField:not(:last-of-type){margin-bottom:5px}.ResidentsInputContainer__fields .MuiTextField-root{flex-grow:1}

.ResidentNameField{display:flex;align-items:center}.ResidentNameField__name{width:100px;margin-right:40px;font-weight:700}

.NumberSelector{display:flex;justify-content:center;align-items:center;margin:15px 0}.NumberSelector__value{height:40px;width:60px;padding:0 15px;font-size:24px;font-weight:600;display:flex;align-items:center;border:1px solid #FFA5B6;border-radius:4px;margin:0 15px}

.VillagersInputContainer{max-width:450px;padding:15px 20px}.VillagersInputContainer h3{margin-bottom:10px}.VillagersInputContainer__fields{margin-top:15px}.VillagersInputContainer__fields .VillagerNameField:not(:last-of-type){margin-bottom:5px}.VillagersInputContainer__fields .MuiTextField-root{flex-grow:1}

